@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Arbutus+Slab&display=swap);
@import url(https://fonts.googleapis.com/css?family=Arbutus+Slab&display=swap);
html{
    overflow: hidden;
}

body {
    margin: 0;
    padding: 0;
    overflow: hidden;
    background: #4e54c8;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #8f94fb, #4e54c8); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}



.App {
  text-align: center;
}

.particles{
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

#tsparticles{
    width: 100vw;
    height: 100vh;
}

.right-tab{
    display: flex;
    height: 100vh;
    /*flex-direction: column;*/
}

.right-tab-arrow{
    /*position: fixed;*/
    width: 20px;
    background: #fff;
    opacity: 0.5;
    z-index: 9999;
}

.right-tab-content{
    width: 50vw;
    background: #000428;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to bottom, #004e92, #000428); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

@media only screen and (max-width: 480px){
    .right-tab-content{
        width: 100vw;
    }
}
#container{
    color: white;
}

#heading{
    margin: 22px 0;
    font-family: Arbutus Slab;
    font-size: 2em;
    color: #DDFC74;
}
.project-item{
    font-family: Arbutus Slab;
    margin: 19px;
    border: 1px solid white;
    border-radius: 6px;
    display: flex;
    padding: 5px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.preview{
    width: 75px;
    height: 75px;
}

.preview>img{
    width: 75px;
    height: 75px;
    border-radius: 10px;
}

.content{
    display: flex;
    flex-direction: column;
    text-align: left;
    padding: 5px;
    padding-left: 10px;
}

.title{
    font-size: 1.7em;
}

.description{
    font-size: 1em;
    width: 34vw;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}
.bottom-tab{
    display: flex;
    flex-direction: column;
    width: 100vw;
    /*position: absolute;
    top: calc(100vh - 20px);*/
    /*flex-direction: column;*/
}

.bottom-tab-arrow{
    position: absolute;
    top:0;
    height: 20px;
    width: 100vw;
    background: #fff;
    opacity: 0.5;
    /*border-top-left-radius: 100%;
    border-top-right-radius: 100%;*/
}

.bottom-tab-content{
    position: absolute;
    top: 20px;
    height: 50vh;
    width: 100%;
    background: #232526;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #414345, #232526); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

@media only screen and (max-width: 480px){
    .bottom-tab-content{
        height: 100vh;
    }
}
