@import url('https://fonts.googleapis.com/css?family=Arbutus+Slab&display=swap');

.project-item{
    font-family: Arbutus Slab;
    margin: 19px;
    border: 1px solid white;
    border-radius: 6px;
    display: flex;
    padding: 5px;
    user-select: none;
}

.preview{
    width: 75px;
    height: 75px;
}

.preview>img{
    width: 75px;
    height: 75px;
    border-radius: 10px;
}

.content{
    display: flex;
    flex-direction: column;
    text-align: left;
    padding: 5px;
    padding-left: 10px;
}

.title{
    font-size: 1.7em;
}

.description{
    font-size: 1em;
    width: 34vw;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}